<template>
  <div class="course_record_list">
    <head-top title="课堂记录列表">
    </head-top>

    <van-pull-refresh v-model="isLoading" @refresh="getCourseRecordList" class="pull_fresh">
      <course-list class="list_card" v-if="courseRecordList && courseRecordList.length>0" :course="courseRecordList">
      </course-list>
      <r-lack-data v-else></r-lack-data>
    </van-pull-refresh>

  </div>
</template>

<script>
import CourseList from "@/components/r-course-list";
import { formatDate } from "@/utils/formatData";

export default {
  name: "courseRecordList",
  components: { CourseList },
  data () {
    return {
      classHourID: this.$store.getters.classHourID,
      courseRecordList: [],
      isLoading: false
    };
  },
  mounted () {
    this.getCourseRecordList();
  },
  methods: {
    getCourseRecordList () {
      this.courseRecordList = [];
      this.isLoading = true;
      this.$mainHttp.classInfo.recordList({
        chID: this.classHourID
      }).then(res => {
        if (res.Result) {
          res.Datas.sort((a, b) => {
            return a.Key["ClassDate"] > b.Key["ClassDate"] ? -1 : 1;
          });

          res.Datas.forEach(item => {
            this.courseRecordList.push({
              id: item.Key["CIID"],
              classID: item.Key["ClassID"],
              classroomID: item.Key["ClassroomID"],
              classroomName: item["ClsRoomName"],
              teacherName: item["TeacherName"],
              className: item["ClassName"],
              classHourName: item["CHName"],
              subjectName: item["CourseName"],
              classDate: item.Key["ClassDate"],
              createTime: formatDate(item.Key["CreateTime"], "yyyy-MM-dd hh:mm")
            });
          })
        } else if (res.Info) {
          this.$toast(res.Info);
        }
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    }
  }
}
</script>

<style lang="less" scoped>
.course_record_list {
  width: 100%;
  background: #fff;
}
</style>
