<template>
  <div class="r-course-list">
    <div v-if="course.length > 0">
      <div class="r-course-item" :key="i" v-for="(item, i) in course">
        <div class="flex-between head">
          <div class="title">{{ item.classHourName }}</div>
          <van-button type="primary" size="mini" @click="selectCourse(item)">查看</van-button>
        </div>
        <div class="course-name">
          【{{item.subjectName}}】
        </div>
        <div class="info-line">
          <div class="line-left">老师：{{ item.teacherName }}</div>
          <div class="line-right">班级：{{ item.className }}</div>
        </div>
        <div class="info-line">
          <div class="line-left">教室：{{ item.classroomName }}</div>
          <div class="line-right">
            时间： {{ item["createTime"] }}
          </div>
        </div>
        <div class="line"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {
    courseType: {
      type: String,
      default: "current"
    },
    course: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  methods: {
    selectCourse (item) {
      this.$store.commit("SET_COURSE", item);
      this.$store.commit("SET_COURSE_ID", item.id);
      this.$router.push("/course-record/record/view");
    }
  },
  computed: {},
  watch: {}
};
</script>
